import {Component, DoCheck, OnInit} from '@angular/core';
import {Book} from '../entity';
import {BookService} from '../book.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-find-bookbyaddress',
  templateUrl: './find-bookbyaddress.component.html',
  styleUrls: ['./find-bookbyaddress.component.css']
})
export class FindBookbyaddressComponent implements OnInit, DoCheck {
  private books: Book[];
  private dataShow: Book[]; // 
  public leng: number;                 // 
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;

  constructor(private bookService: BookService, private route: ActivatedRoute) { }

  ngOnInit() {
    const bookName = this.route.snapshot.params.bookName;
    this.search(bookName);
  }
// DoCheck DoCheck
  // tslint:disable-next-line:use-lifecycle-interface
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for ( let i = 0; i < this.pagNums; i++, startNum++) {
        if ( this.books[startNum] !== null && this.books[startNum] !== undefined) {
          this.dataShow.push(this.books[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }
// 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  search(bookName: string): void { // BUG!!!!!!
    this.bookService.getBookByBookName(bookName)
      .subscribe(res => { this.books = res; this.leng = res.length; });
  }
  delete(book: Book | number): void {
    this.bookService.deleteBook(book)
      .subscribe(res => {
        alert(res.message); // 
        // 
        // if ( res.message === '') { //
        this.books = this.books.filter(h => h !== book); // 
        this.dataShow = [];
        let startNum = (this.currentPag - 1) * this.pagNums;
        for ( let i = 0; i < this.pagNums; i++, startNum++) {
          if ( this.books[startNum] !== null && this.books[startNum] !== undefined) {
            this.dataShow.push(this.books[startNum]);
          }
        }
        this.leng = this.books.length;
      });
  }
}
