import {Component, DoCheck, OnInit} from '@angular/core';
import { Borrowing} from '../entity';
import {BorrowingService} from '../borrowing.service';
import {UserService} from '../user.service';

@Component({
  selector: 'app-return-book',
  templateUrl: './return-book.component.html',
  styleUrls: ['./return-book.component.css']
})
export class ReturnBookComponent implements OnInit, DoCheck {
  private borrowings: Borrowing[];
  private dataShow: Borrowing[];      // 
  public leng: number;                 // ()
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;
  constructor(private borrowingService: BorrowingService, private userService: UserService) { }

  ngOnInit() {
    this.getBorrowings();
  }
  // DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for ( let i = 0; i < this.pagNums; i++, startNum++) {
        if ( this.borrowings[startNum] !== null && this.borrowings[startNum] !== undefined) {
          this.dataShow.push(this.borrowings[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
    console.log( 'curpag:' + pag );
  }
  // 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getBorrowings(): void {
    this.borrowingService.getBorrowingsByReaderId(this.userService.getReader().id).subscribe(
      res => { this.borrowings = res; this.leng = res.length;
         if (this.leng === undefined || this.leng === 0) {
          alert('!');
        }}
    );
  }
  returnBook(borrowing: Borrowing | number): void {
    this.borrowingService.returnBook(borrowing)
      .subscribe(res => {
        alert(res.message); // 
        // 
        // if ( res.message === '') { //
        this.borrowings = this.borrowings.filter(h => h !== borrowing); // 
        this.dataShow = [];
        let startNum = (this.currentPag - 1) * this.pagNums;
        for ( let i = 0; i < this.pagNums; i++, startNum++) {
          if ( this.borrowings[startNum] !== null && this.borrowings[startNum] !== undefined) {
            this.dataShow.push(this.borrowings[startNum]);
          }
        }
        this.leng = this.borrowings.length;
      });
  }
}
