import { Injectable } from '@angular/core';
// Http(get/post/put/delete)
import { HttpClient, HttpHeaders } from '@angular/common/http';
// 
import { Observable, of } from 'rxjs';
// 
import { catchError, map, tap } from 'rxjs/operators';
// 
// tslint:disable-next-line:max-line-length
import {Book, Reader1, Borrowing, Response, Reader} from './entity';
// httpjson
const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

//  
@Injectable({
  providedIn: 'root'
})
export class BorrowingService {
  private borrowingUrl = 'http://localhost:8080/borrowing.ctl';
  constructor(private http: HttpClient) { }

  borrowBook(borrowing: {book: Book; reader: Reader1}): Observable<Response> {
    // @ts-ignore
    return this.http.post<Response>(this.borrowingUrl, borrowing, httpOptions).pipe(
      catchError(this.handleError<Response>('borrowBook'))
    );
  }

  returnBook(borrowing: Borrowing | number): Observable<Response> {
    return this.http.put<Response>(this.borrowingUrl, borrowing, httpOptions).pipe(
      catchError(this.handleError<Response>('updateReader'))
    );
  }
  /**/
  getBorrowingsByReaderId(readerId: number): Observable<Borrowing[]> {
    const url = this.borrowingUrl + '?readerId=' + readerId;
    return this.http.get<Borrowing[]>(url).pipe();
  }
  /**/
  getBorrowingsByReaderName(readerName: string): Observable<Borrowing[]> {
    const url = this.borrowingUrl + '?readerName=' + readerName;
    return this.http.get<Borrowing[]>(url).pipe();
  }
  getBorrowings(): Observable<Borrowing[]> {
    return this.http.get<Borrowing[]>(this.borrowingUrl )
      .pipe(
        catchError(this.handleError<Borrowing[]>('getBorrowings', []))
      );
  }
  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
