import {Component, DoCheck, OnInit} from '@angular/core';
import {Reader} from '../entity';
import {ReaderService} from '../reader.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-find-reader',
  templateUrl: './find-reader.component.html',
  styleUrls: ['./find-reader.component.css']
})
export class FindReaderComponent implements OnInit, DoCheck {
  private readers: Reader[];
  private dataShow: Reader[]; // 
  public leng: number;                 // 
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;

  constructor(private readerService: ReaderService, private route: ActivatedRoute) { }

  ngOnInit() {
    const readerName = this.route.snapshot.params.readerName;
    this.search(readerName);
  }
// DoCheck DoCheck
  // tslint:disable-next-line:use-lifecycle-interface
  // DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for ( let i = 0; i < this.pagNums; i++, startNum++) {
        if ( this.readers[startNum] !== null && this.readers[startNum] !== undefined) {
          this.dataShow.push(this.readers[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
    console.log( 'curpag:' + pag );
  }
  // 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  search(readerName: string): void { // BUG!!!!!!
    this.readerService.getReaderByReaderName(readerName)
      .subscribe(res => { this.readers = res; this.leng = res.length; });
  }
  delete(reader: Reader | number): void {
    this.readerService.deleteReader(reader)
      .subscribe(res => {
        alert(res.message); // 
        // 
        // if ( res.message === '') { //
        this.readers = this.readers.filter(h => h !== reader); // 
        this.dataShow = [];
        let startNum = (this.currentPag - 1) * this.pagNums;
        for ( let i = 0; i < this.pagNums; i++, startNum++) {
          if ( this.readers[startNum] !== null && this.readers[startNum] !== undefined) {
            this.dataShow.push(this.readers[startNum]);
          }
        }
        this.leng = this.readers.length;
      });
  }
}
